html {
  background: transparent;
}
.ldbtn {
  cursor: pointer;
  outline: none;
  border-radius: 100%;
  padding: 0 5px 5px 5px;
  border-color: white;
  border-style: solid;
  color: #FFF;
  background-color: transparent;
}

body {
  background: transparent !important;
}

.ldbtn:focus {
  outline: none;
}

.green{
  color: lightgreen;
  border-color: lightgreen;
}

.red {
  color: red;
  border-color: red;
}

html {
  min-height: 20px;
}